.view-wrapper {
  position: absolute;
  width: 100%;
  height: calc(100% - 10px);
  margin-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center
}

.spinner {
  margin: 20px auto;
  width: 50px;
  height: 40px;
  display: flex;
  justify-content: center
}

.spinner>div {
  background-color: white;
  height: 100%;
  width: 6px;
  margin: 2px 2px;
  animation: sk-stretchdelay 1.2s infinite ease-in-out
}

.spinner .rect2 {
  animation-delay: -1.1s
}

.spinner .rect3 {
  animation-delay: -1.0s
}

.spinner .rect4 {
  animation-delay: -0.9s
}

.spinner .rect5 {
  animation-delay: -0.8s
}

@keyframes sk-stretchdelay {

  0%,
  40%,
  100% {
    transform: scaleY(0.4)
  }

  20% {
    transform: scaleY(1)
  }
}

