.chatbox-view {
  display: flex;
  flex-direction: column;
  height: 150px;
  width: 800px;
  min-width: 300px;
  padding: 5px;
  position: relative
}

@media (max-height: 700px) {
  .chatbox-view {
    height: 130px
  }
}

.chatbox-view>:not(:first-child) {
  margin-top: 5px
}

.chatbox-view>.log {
  position: relative;
  flex-grow: 1;
  overflow: hidden;
  line-height: 1.3
}

.chatbox-view>.log>p:not(:last-child) {
  margin-bottom: 3px
}

.chatbox-view>.log p {
  overflow: hidden
}

.chatbox-view>.log p.notice {
  color: #8ed2ab
}

.chatbox-view>.log p.highlight {
  font-weight: bold
}

.chatbox-view>.log p.announcement {
  border-left: 3px solid #c13535;
  padding-left: 5px;
  min-height: 12px;
  white-space: pre-wrap
}

.chatbox-view>.autocompletebox {
  position: absolute;
  background-color: white;
  line-height: 25px;
  color: black;
  bottom: 30px;
  left: 5px;
  border: solid black 1px;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 200px
}

.chatbox-view>.autocompletebox>div {
  padding: 0px 30px 0px 5px;
  white-space: nowrap
}

.chatbox-view>.autocompletebox>div:hover {
  background-color: #d1e2fe
}

.chatbox-view>.autocompletebox>div.selected {
  background-color: #b3c6ff
}

.chatbox-view>.input {
  display: flex;
  flex-direction: row;
  flex-shrink: 0
}

.chatbox-view>.input input[type="text"] {
  color: white;
  background-color: #111619;
  border: none;
  border-radius: 5px;
  padding: 0 10px;
  margin-right: 4px;
  flex-grow: 1;
  align-self: stretch;
  box-sizing: border-box
}

.chatbox-view>.input input[type="text"]:focus {
  border: 1px solid #244967;
  padding: 0 7px
}

.chatbox-view>.input button {
  background-color: #244967
}

.chatbox-view>.input button:hover {
  background-color: #2f5e85
}

.chatbox-view>.input button:active {
  background-color: #193449
}

.chatbox-view>.input button:disabled {
  background-color: #3e3e3e
}

.replayer .chatbox-view {
  height: 130px
}

.replayer .chatbox-view>.input {
  display: none
}

.connecting-view {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center
}

.connecting-view .dialog,
.connecting-view .room-view>.container {
  height: 300px;
  width: 300px
}

.connecting-view button {
  width: auto;
  align-self: flex-end
}

.connecting-view-log {
  flex: 1
}

.connecting-view-log p {
  margin: 3px 0
}

.disconnected-view {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center
}

.disconnected-view .dialog,
.disconnected-view .room-view>.container {
  width: 300px
}

.disconnected-view .dialog>p,
.disconnected-view .room-view>.container>p {
  margin-bottom: 20px
}

.disconnected-view button {
  width: auto;
  align-self: flex-end
}

.game-view {
  position: absolute;
  /* left: 50%; */
  top: 35px;
  width: 100vw;
  height: calc(100vh - 35px);
  overflow: hidden;
  display: flex;
  /* display: none; */
  flex-direction: column;
  align-items: stretch
}

.game-view>.top-section {
  position: relative;
  flex-grow: 1;
  left: 100%;
  min-height: 0
}

.game-view>.bottom-section {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  background-color: #1A2125;
  position: relative;
  left: 100%
}

.game-view>.bottom-section>.buttons {
  padding: 6px;
  white-space: nowrap;
  margin-right: auto;
  display: flex;
  flex-direction: column
}

.game-view>.bottom-section>.buttons>:not(:last-child) {
  margin-bottom: 6px
}

.game-view>.bottom-section button {
  background-color: #244967
}

.game-view>.bottom-section button:hover {
  background-color: #2f5e85
}

.game-view>.bottom-section button:active {
  background-color: #193449
}

.game-view>.bottom-section button:disabled {
  background-color: #3e3e3e
}

.game-view>.bottom-section .stats-view {
  margin-right: auto
}

.game-view.replayer>.bottom-section {
  padding-bottom: 40px
}

.game-view>[data-hook="popups"] {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(115, 136, 92, 0.5)
}

.flexRow>[data-hook="popups"] {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(115, 136, 92, 0.5)
}

.redStats {
  text-align: right;
  color: red
}

.blueStats {
  color: #5688e5;
}

.center {
  text-align: center;
}

.leftStat {
  text-align: left;
  width: 100;
}

.centerStat {
  text-align: center;
  width: 100;
}

.loader-view {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center
}

.loader-view .dialog,
.loader-view .room-view>.container {
  width: 300px
}

.stats-view {
  background-color: #1A2125;
  padding: 5px;
  margin: 0 5px;
  white-space: nowrap;
  flex: 0 0 100px;
  overflow: hidden;
  line-height: 1.5em
}

.stats-view .graph {
  width: 100%
}

.stats-view .graph canvas {
  width: 100%;
  height: 64px;
  image-rendering: optimizeSpeed;
  image-rendering: crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor
}

.choose-nickname-view {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center
}

.choose-nickname-view>img {
  flex: 0 0 auto;
  position: relative;
  left: 20px;
  margin-bottom: 20px
}

.choose-nickname-view>.dialog,
.room-view.choose-nickname-view>.container {
  width: 300px
}

.choose-nickname-view>.dialog>[data-hook=ok],
.room-view.choose-nickname-view>.container>[data-hook=ok] {
  align-self: center;
  width: 100px
}

.game-timer-view {
  display: flex;
  align-items: center
}

.game-timer-view>.overtime {
  margin-right: 20px;
  visibility: hidden
}

.game-timer-view>.overtime.on {
  visibility: visible;
  animation: 0.4s linear overtime-appear
}

.game-timer-view>.digit {
  width: 15px;
  text-align: center
}

.game-timer-view.time-warn {
  animation: 0.3s infinite alternate linear time-warn
}

@keyframes time-warn {
  from {
    color: white
  }

  to {
    color: #ff8080
  }
}

@keyframes overtime-appear {
  from {
    opacity: 0;
    transform: translateY(-25px)
  }
}

.game-state-view {
  position: absolute;
  width: 100%;
  height: 100%
}

.game-state-view .bar-container {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 1
}

.game-state-view .bar {
  display: flex;
  justify-content: center;
  background-color: #1A2125;
  padding: 5px;
  border-radius: 0 0 5px 5px;
  font: 900 20px Arial Black, Arial Bold, sans-serif
}

.game-state-view .bar>.scoreboard {
  display: flex;
  align-items: center;
  margin-right: 100px
}

.game-state-view .bar>.scoreboard .score {
  width: 35px;
  overflow: hidden;
  text-align: center
}

.game-state-view .bar>.scoreboard .teamicon {
  width: 23px;
  height: 23px;
  border-radius: 5px
}

.game-state-view .bar>.scoreboard .teamicon.blue {
  background-color: #5689E5
}

.game-state-view .bar>.scoreboard .teamicon.red {
  background-color: #E56E56
}

.game-state-view canvas {
  width: 100%;
  height: 100%;
  image-rendering: optimizeSpeed;
  image-rendering: crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor
}

.game-state-view.restricted {
  background-color: #2e343c;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center
}

.game-state-view.restricted .bar-container {
  position: relative;
  height: 35px
}

.game-state-view.restricted .bar {
  background: none
}

.game-state-view.restricted canvas {
  width: 100%;
  height: calc(100% - 35px);
  max-width: 840px;
  max-height: 410px
}

.room-view {
  position: absolute;
  width: 100%;
  height: calc(100% - 10px);
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  user-select: none
}

.room-view>.container {
  position: relative;
  min-height: 0;
  max-width: 900px;
  max-height: 550px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding-bottom: 30px;
  z-index: 0
}

.room-view>.container>:not(:last-child) {
  margin-bottom: 15px
}

.room-view>.container>.header-btns {
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px
}

.room-view>.container>.header-btns>:not(:last-child) {
  margin-right: 6px
}

.room-view>.container>.header-btns [data-hook="rec-btn"] i {
  font-size: 75%;
  color: #888;
  margin-right: 4px
}

.room-view>.container>.header-btns [data-hook="rec-btn"].active i {
  color: #D33
}

.room-view>.container>.teams {
  margin-left: -3px;
  margin-right: -3px;
  display: flex;
  flex: 1;
  justify-content: center;
  min-height: 60px
}

.room-view>.container>.teams>* {
  margin-left: 3px;
  margin-right: 3px
}

.room-view>.container>.teams>.tools {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80px
}

.room-view>.container>.teams>.tools>:not(:last-child) {
  margin-bottom: 6px
}

.room-view>.container>.teams>.spacer {
  flex: 1 1 0;
  max-width: 83px;
  margin: 0 0
}

.room-view>.container>.teams .player-list-view {
  min-width: 20px;
  width: 220px;
  display: flex;
  flex-flow: column
}

.room-view>.container>.teams .player-list-view>:not(:last-child) {
  margin-bottom: 6px
}

.room-view>.container>.teams .player-list-view .buttons {
  display: flex;
  justify-content: center;
  flex-flow: row nowrap
}

.room-view>.container>.teams .player-list-view .buttons button[data-hook="join-btn"] {
  min-width: 100px
}

.room-view>.container>.teams .player-list-view .list {
  flex: 1;
  background-color: #111619;
  overflow-x: hidden;
  overflow-y: auto
}

.room-view>.container>.teams .player-list-view.t-red button[data-hook="join-btn"] {
  color: #ff8686
}

.room-view>.container>.teams .player-list-view.t-red button[data-hook="reset-btn"] {
  margin-left: 6px
}

.room-view>.container>.teams .player-list-view.t-red button[data-hook="reset-btn"]:after {
  content: "\25b6"
}

.room-view>.container>.teams .player-list-view.t-blue .buttons {
  flex-direction: row-reverse
}

.room-view>.container>.teams .player-list-view.t-blue button[data-hook="join-btn"] {
  color: #b7b7ff
}

.room-view>.container>.teams .player-list-view.t-blue button[data-hook="reset-btn"] {
  margin-right: 6px
}

.room-view>.container>.teams .player-list-view.t-blue button[data-hook="reset-btn"]:after {
  content: "\25c0"
}

.room-view>.container>.settings {
  width: 300px;
  align-self: center;
  white-space: nowrap
}

.room-view>.container>.settings>div {
  line-height: 22px;
  margin-bottom: 5px;
  display: flex;
  width: 100%;
  align-items: center
}

.room-view>.container>.settings>div>.lbl {
  flex: 0 0 90px
}

.room-view>.container>.settings>div>.val {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis
}

.room-view>.container>.settings>div>select {
  width: 100px
}

.room-view>.container>.settings [data-hook="stadium-name"].custom {
  color: #ffdb72
}

.room-view>.container>.controls {
  display: flex;
  align-self: center
}

.room-view>.container>.controls>* {
  margin: 0 3px
}

.room-view .player-list-item {
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 20px;
  vertical-align: middle;
  padding: 4px;
  margin: 2px;
  font-size: 15px;
  white-space: nowrap
}

.room-view .player-list-item.admin [data-hook="name"] {
  color: #ffdb72
}

.room-view .player-list-item .flagico {
  margin-right: 3px;
  position: relative;
  top: 1px
}

.room-view .player-list-item [data-hook="name"] {
  min-width: 10px;
  overflow: hidden;
  flex: 1
}

.room-view .player-list-item [data-hook="ping"] {
  min-width: 30px;
  text-align: right
}

.room-view .player-list-item:hover {
  background-color: #1b2328
}

.room-view button {
  background-color: #244967
}

.room-view button:hover {
  background-color: #2f5e85
}

.room-view button:active {
  background-color: #193449
}

.room-view button:disabled {
  background-color: #3e3e3e
}

.room-view button[data-hook="start-btn"] {
  background-color: #3a9933
}

.room-view button[data-hook="start-btn"]:hover {
  background-color: #46b83d
}

.room-view button[data-hook="start-btn"]:active {
  background-color: #2e7a29
}

.room-view button[data-hook="start-btn"]:disabled {
  background-color: #3e3e3e
}

.room-view button[data-hook="stop-btn"] {
